.balance {
    display: inline-block;
    transition: transform 0.3s;
  }
  
.balance:hover {
    animation: wiggle 0.4s ease-in-out infinite;
  }
  
  @keyframes wiggle {
    0% { transform: rotate(-5deg); }
    50% { transform: rotate(5deg); }
    100% { transform: rotate(-5deg); }
  }
  