.container {
  display: grid;
  place-items: center;
}

.increase {
  transition: 0.6;
  animation: ease-in;
}

.increase:hover {
  transform: scale(1.1);
  box-shadow: 0 0 10px 10px rgba(0, 0, 0, 0.5);
}

.card-container {
  display: flex;
  justify-content: space-between;
}

.card {
  width: 200px; /* Ajusta el ancho según tus necesidades */
  margin-right: 20px; /* Espacio entre las tarjetas */
}


.container {
        display: flex;
