.gradient-metalico {
      background: linear-gradient(45deg, #434343, #000000);
      background-color: #000000;
      background-image: radial-gradient(circle, #333333, #000000);
      background-blend-mode: multiply;
      color: #ffffff;
      padding: 20px;
      text-align: center;
      font-size: 24px;
      font-weight: bold;
    }
