.container {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
  }
  
  .engrane {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    position: relative;
    animation: rotate 3s linear infinite;
  }
  
  .engrane::before,
  .engrane::after {
    content: "";
    position: absolute;
    top: 0;
    left: 50%;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    transform: translate(-50%, -50%);
  }
  
  .engrane::before {
    top: 40px;
  }
  
  .engrane::after {
    top: 60px;
  }
  
  @keyframes rotate {
    0% {
      transform: rotate(0);
    }
    100% {
      transform: rotate(1turn);
    }
  }
  