@use 'assets/styles';
@use '@gear-js/ui/variables' as *;

#root {
  max-width: 1400px;
  min-height: 100vh;
  margin: 0 auto;
  padding: 0 16px;
  display: flex;
  flex-direction: column;
  box-sizing: content-box;
  background:black; 
}

main {
  flex: 1;
  padding: 30px 0 60px;
  position: relative;
  background: $bgColor url(assets/images/bg.svg); 
  background-color: black;
}

body {
  background: black;;
}
